.langbar_container {
    height: 2.5rem;
    z-index: 100;
    width: 100%;
    background-color: var(--color-bg);
    box-shadow: 0 3px 3px rgba(0, 0, 0, 0.3);
    display: flex;
    gap: 0.25rem;
    justify-content: end;
    padding: 0.25rem;
}

.langbar_container h3 {
    margin: auto 0;
    color: var(--color-light);
}

.fi {
    font-size: 1.5rem;

    border-radius: 50%;
}

.langbar_container .langbar_btn {
    padding: 0.1rem;
    border: 1px solid transparent;
}

.langbar_container span {
    font-size: 1.5rem;
}

.langbar_container button.active {
    background: var(--color-white);
}